<template>
  <section class="hero" @mousemove="animationText" @mouseleave="animationTextReset">
    <div class="hero-container container">
      <a href="#" @click.prevent="goto('cases')" class="hero-scroll">
        <svg width="24" height="35" viewBox="0 0 24 35" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="1.5" y="1.5" width="21" height="32" rx="10.5" stroke="#333333" stroke-width="3"/>
          <rect x="11.75" y="19.75" width="1.5" height="4.5" rx="0.75" stroke="#333333" stroke-width="1.5"/>
        </svg>

        <span>
                    Scroll Down
                </span>
      </a>

      <div>
        <h1 class="main-title">
          <span class="animate-text">Portfolio</span>
          <span>of Malykh Vitaliy</span>
        </h1>

        <h2 style="display: none" class="hero-subtitle">
                    <span class="hero-svg-container">
                        <svg xmlns="http://www.w3.org/2000/svg" fill="none" aria-hidden="true" viewBox="0 0 24 24"
                             role="img">
                            <path vector-effect="non-scaling-stroke" stroke="#fff"
                                  stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="10"
                                  stroke-width="1.5"
                                  d="M17.911 18.594c.3 1.096-.897 1.893-1.793 1.295l-4.086-2.69-4.085 2.69c-.897.598-2.092-.299-1.793-1.295L7.25 13.91l-3.787-3.09c-.896-.697-.398-2.092.698-2.191l4.882-.3 1.794-4.583c.398-.996 1.893-.996 2.291 0l1.794 4.584m2.491.3v6.974M21 12.117h-6.975"></path>
                        </svg>
                    </span>

          <span>top rated plus</span>
        </h2>

        <div class="info_inner">
          <div class="info_inner_list">
            <div class="info_inner_item">
                    <span class="title">
                      Успішних проектів
                    </span>
              <span class="procent">
                      100%
                    </span>
            </div>
            <div class="info_inner_item">
                    <span class="title">
                      Середня оцінка
                    </span>
              <span class="procent">
                      9.9 з 10
                    </span>
            </div>
          </div>
        </div>


      </div>

      <div class="hero-img">
        <img :src="require('@/assets/images/hero-person-orange.webp')" alt="hero person">
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "HeroComponent",
  methods: {
    goto(id) {
      document.getElementById(id).scrollIntoView({
        behavior: "smooth"
      });
    },
    animationText(e) {
      let text = document.querySelector('.animate-text');

      let x = (e.clientX / 12);
      let y = (e.clientY / 12) - 210;

      window.requestAnimationFrame(() => {
        text.style.left = x + 'px';
        text.style.top = y + 'px';
      });
    },
    animationTextReset() {
      let text = document.querySelector('.animate-text');
      const mediaQuery = window.matchMedia('(max-width: 1500px)')


      window.requestAnimationFrame(() => {
        text.style.left = '0';
        text.style.top = '-210px';
        if (mediaQuery.matches) {
          text.style.top = '-170px';
        }
      });
    }
  }
}
</script>

<style scoped lang="scss">
.hero {
  height: 100vh;
  background-image: url("@/assets/images/hero-bg.webp");
  background-size: cover;
  background-position: bottom center;
  background-repeat: no-repeat;
  padding-top: 180px;

  .info_inner {
    display: flex;
    align-items: baseline;
    justify-content: flex-end;
  }
  .info_inner img{
    margin-top: 0;
  }

  .info_inner_list {
    display: flex;
    align-items: baseline;
    column-gap: 10px;
  }

  .info_inner_item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #FBFBFB;
    padding: 20px 40px;
    row-gap: 10px;
  }

  .info_inner_item .title {
    font-family: $Gilroy;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.2px;
    text-align: center;

  }

  .info_inner_item .procent {
    font-family: $Gilroy;
    font-size: 24px;
    font-weight: 700;
    line-height: 29.71px;
    text-align: left;
    color: #FFBB17;

  }

  .hero-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 60px;
    height: 100%;
  }

  .hero-svg-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 28px;
    min-width: 28px;
    height: 16px;
    background-color: #1f57c3;
    position: relative;

    svg {
      z-index: 1;
      width: 16px;
    }

    &::before, &::after {
      content: '';
      width: 28px;
      min-width: 28px;
      height: 16px;
      background-color: #1f57c3;
      transform: translate(-50%, -50%) rotate(60deg);
      position: absolute;
      top: 50%;
      left: 50%;
    }

    &::after {
      transform: translate(-50%, -50%) rotate(-60deg);
    }
  }

  img {
    width: 100%;
    max-width: 100%;
    height: auto;
    margin-top: -70px;
  }

  .main-title {
    position: relative;
    white-space: nowrap;
    pointer-events: none;
  }

  .animate-text {
    position: absolute;
    top: -210px;
    left: 0;
    display: block;
    font-family: $Caveat;
    font-style: normal;
    font-weight: 700;
    font-size: 238.829px;
    line-height: 100%;
    color: #ffbb17;
    transform: rotate(-4.61deg);
    text-shadow: -6px 0 #FFFFFF, 0 6px #FFFFFF, 6px 0 #FFFFFF, 0 -6px #FFFFFF;
    transition: .4s linear;
  }

  .hero-subtitle {
    font-size: 32px;
    color: #828282;
    line-height: 32px;
    padding-right: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 10px;
  }

  .hero-scroll {
    position: absolute;
    left: 15px;
    bottom: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;

    svg {
      transition: .3s;
    }

    @include hovers {
      svg {
        fill: #FFBB17;
      }
    }
  }


  @include breakpoint(large) {
    .hero-container {
      gap: 20px;
    }

    .main-title {
      font-size: 100px;

      .animate-text {
        top: -170px;
        font-size: 194px;
      }
    }
  }

  @include breakpoint(small) {
    .main-title {
      font-size: 70px;

      .animate-text {
        top: -120px;
        font-size: 135px;
        text-shadow: -4px 0 #FFFFFF, 0 4px #FFFFFF, 4px 0 #FFFFFF, 0 -4px #FFFFFF;
      }
    }

    .hero-subtitle {
      padding-right: 5px;
    }
  }

  @include breakpoint(extra-small) {
    background-size: cover;

    .hero-container {
      padding-bottom: 20px;
    }

    .main-title {
      font-size: 50px;

      .animate-text {
        top: -85px;
        font-size: 96px;
      }
    }

    .hero-subtitle {
      font-size: 24px;

      svg {
        width: 20px;
      }
    }
  }

  @include breakpoint(small-tablet-portrait) {
    background-image: url("@/assets/images/hero-mobile-bg.webp");
    min-height: 600px;

    .hero-container {
      flex-direction: column-reverse;
      gap: 60px;
    }

    .hero-img {
      max-width: 300px;
    }

    .main-title {
      font-size: 50px;

      .animate-text {
        top: -83px;
        font-size: 97px;
        text-shadow: -2px 0 #FFFFFF, 0 2px #FFFFFF, 2px 0 #FFFFFF, 0 -2px #FFFFFF;
      }
    }

    .hero-scroll {
      position: unset;
    }
  }
}
</style>